import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';


export default function Navbar(props) {
  return (
    <>
      <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">{props.title}</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">{props.about}</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/About">About</Link>
              </li>
            </ul>
              <div className="bg-primary rounded mx-2" onClick={()=> props.toggleMode('primary')} style={{width:'30px',height:'30px',cursor:'pointer'}}></div>
              <div className="bg-success rounded mx-2" onClick={()=> props.toggleMode('success')} style={{width:'30px',height:'30px',cursor:'pointer'}}></div>
              <div className="bg-danger rounded mx-2" onClick={()=> props.toggleMode('danger')} style={{width:'30px',height:'30px',cursor:'pointer'}}></div>
              <div className="bg-warning rounded mx-2" onClick={()=> props.toggleMode('warning')} style={{width:'30px',height:'30px',cursor:'pointer'}}></div>
              <div className="bg-light rounded mx-2" onClick={()=> props.toggleMode('light')} style={{width:'30px',height:'30px',cursor:'pointer'}}></div>
              <div className="bg-dark rounded mx-2" onClick={()=> props.toggleMode('dark')} style={{width:'30px',height:'30px',cursor:'pointer'}}></div>
                  
            <div className={`form-check form-switch text-${props.mode === 'light' ? 'black' : 'light'}`}>
              {/* <input className="form-check-input" onClick={props.toggleMode} type="checkbox" id="flexSwitchCheckDefault" /> */}
              {/* <label className={`form-check-${props.text === 'light' ? 'black' : 'light'}`} HtmlFor="flexSwitchCheckDefault" onChange={props.text}>{props.text}</label> */}
            </div>
          </div>
        </div>
      </nav>

    </>
  );
}

Navbar.propTypes = {
  title: PropTypes.string.isRequired,
  about: PropTypes.string.isRequired
};

Navbar.defaultProps = {
  about: 'Home',
};

